

.event{
    justify-content: center;
    transition: all 0.3s ease-in-out;
    font-size: medium;
    font-weight: bolder;
    background: linear-gradient(5deg, rgb(241, 248, 198) 1%,rgb(175, 233, 197) 90%);
    border-radius: 20px;
    padding: auto;
    border: 8px solid rgb(229, 241, 227);
    box-shadow:3px 3px 2px rgb(133,189,215, 0.8784313725);
    margin: 10px;
    color: black;
    border-color: rgb(202, 226, 207);

}



.diksevent  {
    background: linear-gradient(5deg, rgb(241, 248, 198) 1%,rgb(37, 177, 100) 90%);
    justify-content: center;
    border: 4px solid rgb(229, 241, 227);

}



.btn {
    justify-content: center;
    transition: all 0.3s ease-in-out;
    font-size: x-large;
    font-weight: bolder;
    border-radius: 10px 100px/120px;
    background: linear-gradient(5deg, rgb(241, 248, 198) 1%,rgb(196, 13, 22) 50%);
    padding: auto;
    border: 8px solid rgb(241, 227, 229);
    box-shadow: 3px 3px 2px rgba(184, 7, 16, 0.878);
    margin: 1px;
    color: black;
    overflow: hidden;
    border-color: darkgreen;
    
  }
  .btn:hover {
    background: linear-gradient(5deg, rgb(241, 248, 198) 1%,rgb(37, 177, 100) 90%);
   

transform:scaleY(1.3);
  }

  .btn:hover::before {
    opacity: 1;

}



  .calander{
   
      background: linear-gradient(50deg, rgb(241, 248, 198) 1%,rgb(27, 128, 72) 60%);
      box-shadow:3px 3px 2px rgb(133,189,215, 0.8784313725);

      border-radius: 10px 5px/5px;


   
    
  }
  
  @keyframes anglerotate{
    from{

        rotate: 0deg;
    }
    to{
        rotate:360deg;

    }
  }
