.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: linear-gradient(5deg, rgb(167, 233, 192) 5%,rgb(2, 66, 27) 90%);
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(5px + 2vmin);
  color: white;
  animation-name: myAnimationColor, App-logo-spin;
  animation-duration: 3s;
  border-radius: 10px 100px/120px;

}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: scale(0.5);
  }

  to {
    transform: scale(1);
  }

}

@keyframes myAnimationColor {
  from {
    background: linear-gradient(5deg, rgb(185, 167, 233) 1%,rgb(2, 66, 27) 10%);

  }

  to {
    background: linear-gradient(50deg, rgb(167, 233, 192) 5%,rgb(2, 66, 27) 90%);

  }
}

.tanaste-container {
  width: 200px;
  height: 200px;
  border-radius: 50%; /* Macht es rund */
  background: conic-gradient(rgb(79, 223, 22) 0deg, rgb(43, 5, 182) 90deg, rgb(9, 255, 0) 180deg, rgb(207, 195, 19) 270deg, rgb(252, 227, 8) 360deg); /* Beispiel-Gradient */
  position: relative; /* Für Zeiger-Positionierung */
}

/* Beispiel für einen Zeiger (Stundenzeiger) */
.tanaste {
  position: absolute;
  width: 4px;
  height: 50px; /* Länge */
  background: rgb(238, 12, 12);
  transform-origin: bottom center; /* Zeiger dreht sich von der Mitte */
  top: 50px;
  left: 98px;
  animation: rotateHour 450s linear infinite; /* Animiert jede Tanaste  (tanaste =450s)*/
}
.tanaste2  {
  position: absolute;
  width: 4px;
  height: 50px; /* Länge */
  background: rgb(250, 248, 248);
  transform-origin: bottom center; /* Zeiger dreht sich von der Mitte */
  top: 50px;
  left: 98px;
  animation: rotateHour 60s linear infinite; /* Animiert jede Tanaste  (tanaste =450s)*/
}
.taggourte  {
  position: absolute;
  width: 4px;
  height: 50px; /* Länge */
  background: rgb(1, 54, 13);
  transform-origin: bottom center; /* Zeiger dreht sich von der Mitte */
  top: 50px;
  left: 98px;
  animation: rotateHour 12h linear infinite; /* Animiert jede Tanaste  (tanaste =450s)*/
}
.seconde  {
  position: absolute;
  width: 4px;
  height: 50px; /* Länge */
  background: rgb(7, 43, 245);
  transform-origin: bottom center; /* Zeiger dreht sich von der Mitte */
  top: 50px;
  left: 98px;
  animation: rotateHour 1s linear infinite; /* Animiert jede Tanaste  (tanaste =450s)*/
}
@keyframes rotateHour {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
.number {
  position: absolute;
  font-size: 24px;
  transform: translate(-50%, -50%);
}
.number1  { top: 50%; left: 97%; }
.number2  { top: 85%; left: 50%; }
.number3  { top: 50%; left: 3%; }
.number0 { top: 15%; left: 50%; }
